<template lang="pug">
div( :style="{ backgroundColor: bgColor }" )
</template>
    
<script>
export default {
  name: 'square',
  props: {
    color: {
      type: String,
      required: true,
      validator: function (value) {
        return ['light-red-200','light-red-300','light-red-400','light-red-500','light-red-bricks-red',
          'dark-red-bricks-red','dark-red-700','dark-red-800','dark-red-900','dark-red-1000',
          'gray-shade-dark','gray-shade-deep','gray-shade-mid','gray-shade-normal','gray-shade-soft','gray-shade-light',
          'white-normal',
          '200','300','400','500','bricks-red','700','800','900','1000',
          'dark','deep','mid','normal','soft','light','white',
        ].indexOf(value) !== -1;
      },
    },
  },

  computed: {
    bgColor() {
      return `var(--${this.$color})`
    },
  },
};
</script>
    
<style scoped lang="sass">
@import './color'
</style>
    